body{
    margin: auto;
    background-color:#eeded8 ;
}

#search{
    height:500px;
    width:100%;
    background-image:url('https://i.ibb.co/gJbF4yH/food-wallpaper.png');
    background-repeat:no-repeat;
    background-size: cover;
    background-size: 100% 500px;
}
#logo{
    height:150px;
    width:150px;
    background-color:white;
    margin-left:45%;
    margin-top:4%;
    border-radius: 50%;
    font-size:110px;
    text-align: center;
    position: absolute;
    color:red;
}


#logo > span{
    margin-top:0%;
    margin-left:-21%;
    position: absolute;
    color:#b05b3a
}

#heading{
    color:white;
    font-size:30px;
    text-align: center;
    margin-left: 35%;
    margin-top: 16%;
    background-color:rgb(60, 168, 231);
    width:30%;
    position: absolute;
}

#dropdown{
    height:60px;
    width:50%;
    margin: auto;
    margin-top:2%;
    position: absolute;
    margin-left: 30%;
    margin-top: 20%;
}

select{
    height: 60px;
    width:40%;
    font-size:20px
}

.restDrop{
    width:58%;
    margin-left:1%
}

a{
    text-decoration: none;
}

@media screen and (max-width:1600px){
    body{
        background-color:rgb(203, 245, 203)
    }
}

@media screen and (max-width:1400px){
    body{
        background-color:rgb(226, 184, 245)
    }
}

@media screen and (max-width:1200px){
    body{
        background-color:rgb(131, 243, 228)
    }
    .tileContainer{
        box-shadow:10px 15px 20px rgb(88, 91, 92);
    }
    #heading{
        width:40%
    }
    
}
@media screen and (max-width:1000px){
    body{
        background-color:rgb(147, 156, 238)
    }
    .tileContainer{
        box-shadow:10px 15px 20px rgb(240, 242, 243);
        width:45%;
    }
    .componentHeading > a{
        text-decoration: none;
        color:white;
    }
    .componentHeading{
        color:white;
    }
}
